import React, { Component } from 'react';

class Details extends Component {
	render() {
		return (
			<div className="frame">
				<h3>Details</h3>
				<h2>{this.props.values.title}</h2>
				<ul>
					<li>{this.props.values.value} bps</li>
					<li>
						{this.props.values.percent} of total {this.props.values.total}
					</li>
				</ul>
			</div>
		);
	}
}
export default Details;
