import React, { Component } from 'react';
import Multibar from './components/Multibar';
import Details from './components/Details';
import './components/App.css';

class App extends Component {
	constructor() {
		super();
		this.handleOnHover = this.handleOnHover.bind(this);
		this.state = {
			values: {
				title: '',
				value: '',
				percent: '',
				total: ''
			}
		};
	}
	handleOnHover(e) {
		console.log(e.target);

		this.setState({
			values: {
				title: e.target.dataset.title,
				value: e.target.dataset.value,
				percent: e.target.dataset.percent + '%',
				total: e.target.dataset.total
			}
		});

		//console.log(this.state.values);
	}

	loading() {
		return new Promise(resolve => setTimeout(resolve, 1000));
	}

	componentDidMount() {
		this.loading().then(() => {
			const loading = document.getElementById('loading');
			if (loading) {
				loading.classList.add('content-available');
				setTimeout(() => {
					loading.outerHTML = '';
				}, 500);
			}
		});
	}

	render() {
		const values = [
			{
				title: 'Data1',
				left: { color: '#007cff', value: 48 },
				right: { color: '#ffe944', value: 240 }
			},
			{
				title: 'Data2',
				left: { color: '#007cff', value: 345 },
				right: { color: '#ffe944', value: 123 }
			},
			{
				title: 'Data3',
				left: { color: '#007cff', value: 100 },
				right: { color: '#a1e104', value: 333 }
			}
		];
		return (
			<div className="wrapper">
				<Multibar values={values} onhover={this.handleOnHover} />
				<Details values={this.state.values} className="frame" />
			</div>
		);
	}
}

export default App;
